.app__specialMenu {
  flex-direction: column;
  background: var(--color-black);
}

.app__specialMenu-title {
  margin-bottom: 2rem;
  text-align: center;
}

.app__specialMenu-name {
  flex: 1;
}

.app__specialMenu-menu {
  width: 100%;
  margin: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
}

.app__specialMenu-menu_heading {
  font-family: var(--font-base);
  font-weight: 600;
  font-size: 45px;
  line-height: 58.5px;
  letter-spacing: 0.04em;
  color: var(--color-white);
}

.app__specialMenu-menu_img {
  width: 410px;
}

.app__specialMenu-menu_img img {
  object-fit: scale-down;
  width: 100%;
}

.app__specialMenu-menu_wine,
.app__specialMenu-menu_cocktail {
  flex: 1;
  width: 100%;
  flex-direction: column;
}

.app__specialMenu-menu_items {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 2rem 0;
}

@media screen and (max-width: 1200px) {
  .app__specialMenu-menu {
    flex-direction: column;
    align-items: center;
  }
  .app__specialMenu-menu_img {
    margin: 3rem 0;
  }
}

@media screen and (max-width: 650px) {
  .app__specialMenu-menu {
    max-width: 100%;
    flex-direction: column;
    align-items: center;
  }
  .app__specialMenu-menu_img img {
    margin-bottom: 2rem;
  }
}

@media screen and (max-width: 450px) {
  .app__specialMenu-menu_img {
    width: 300px;
  }
}
